.panigation-list{
    list-style: none;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.panigation-item{
   border: 1px solid #d9d9d9;
   padding: 10px 15px;
   margin: 0 2px;
   border-radius: 4px;
   cursor: pointer;

}
.panigation-item-p{
   width: 10px;
    display: flex;
    justify-content: center;
}
.panigation-active{
    border: 1px solid #d9d9d9;
    font-weight: bold;
    background-color: #f3f4f5;
}
